import React, {useState} from 'react';
import PropTypes from "prop-types";

const AddLesson = ({createLesson, category}) => {
  const [lessonTitle, setLessonTitle] = useState();

  const handleSubmit = (e) => {
    e.preventDefault()
    createLesson({lessonTitle, category: category._id});
  }

  return (
    <div className={'add-lesson-form add-form'}>
      <form className='form' onSubmit={(e) => handleSubmit(e)}>
        <div className={'inputs-wrapper'}>
          <div className="form-group field">
            <input autoComplete='off' type="input" className="form-field" placeholder=" " name="login" id='login'
                   onChange={e => setLessonTitle(e.target.value)} required/>
            <label htmlFor="login" className="form-label"> </label>
          </div>

          <div className='submit-btn-wrapper'>
            <button type="submit" className='btn' id='addLesson'>
              <span></span>
            </button>
          </div>
        </div>
      </form>
    </div>
  )
}

AddLesson.propTypes = {
  createLesson: PropTypes.func.isRequired,
  category: PropTypes.object.isRequired
};

export default AddLesson;